import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link, HashRouter } from 'react-router-dom'
import { URL_HOME } from '../util/constant'

const BodyBackgroundColor = '#F9F9F9'
const FORM_STYLE = {
    margin: "0 auto",
    padding: 30,
    width: 300,
    border: "1px solid #D7DDE1",
    borderRadius: "7px",
    backgroundColor: "white"
    // marginLeft: "auto",
    // marginRight: "auto",
    // marginTop: "auto",
    // marginBottom: "auto"
    // margin-left:auto,
    // margin-right:auto;
    // backgroundColor: "red"
}

const SIGNUP_LINK_STYLE = {
    display: "inline-block",
    marginLeft: 10
}

class Login extends Component {
    constructor(props) {
        super(props)
    }

    state = {
        id: '',
        pw: ''
    }

    componentWillMount = () => {
        document.body.style.backgroundColor = BodyBackgroundColor
    }

    handleIdChange = (e) => {
        const v = e.target.value
        this.setState({ id: v })
    }

    handlePwChange = (e) => {
        const v = e.target.value
        this.setState({ pw: v })
    }

    handleLoginOnClick = (e) => {
        const { id, pw } = this.state
        if (id === '' || pw === '') {
            this.setState({
                id: '',
                pw: ''
            })
            alert('22' + this.state.id + ', ' + this.state.pw)
            this.etxId.focus()
        } else {
            this.props.history.push(URL_HOME)
        }
    }

    render() {
        return (
            <form>
                <div style={{ width: 20, height: 60, padding: '10px', margin: "0 auto" }}>

                </div>
                <div style={FORM_STYLE}>
                    <div className="field">
                        <label className="label"></label>
                        <div className="control">
                            <input className="input" type="text" placeholder=""
                                value={this.state.id}
                                onChange={this.handleIdChange}
                                ref={(ref) => this.etxId = ref} />
                        </div>
                    </div>

                    <div className="field">
                        <label className="label"></label>
                        <div className="control">
                            <input className="input" type="text" placeholder=""
                                value={this.state.pw}
                                onChange={this.handlePwChange}
                                onKeyPress={(e) => {
                                    if (e.key == 'Enter') {
                                        this.handleLoginOnClick()
                                    }
                                }} />
                        </div>
                    </div>

                    <div className="field">
                        {/* type=submit default     form   button   */}
                        <button className="button" type="button" onClick={this.handleLoginOnClick}>login</button>
                    </div>
                </div>
            </form>
        );
    }
}

Login.propTypes = {

};

export default Login;
